<template>
  <b-modal
    id="contact"
    title="Contact Form"
    ref="contactModal"
    class="form form--contact"
    title-sr-only
    hide-footer
    hide-backdrop
  >
    <div class="form__messages text-center">
      <b-spinner v-if="show.loading" label="Submitting form"></b-spinner>

      <div v-if="show.failure" class="form__message form__message--failure">
        {{ message.failure }}
      </div>
      <div v-if="show.success" class="form__message form__message--success">
        {{ message.success }}
      </div>
    </div>

    <b-form @submit="onSubmit" v-if="show.form">
      <b-form-group id="Name" label="Name:" label-for="NameInput">
        <b-form-input
          id="NameInput"
          v-model="fields.name"
          placeholder="Full Name"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="Email" label="Email:" label-for="EmailInput">
        <b-form-input
          id="EmailInput"
          v-model="fields.email"
          type="email"
          placeholder="example@gmail.com"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="Message" label="Message:" label-for="MessageInput">
        <b-form-textarea
          id="MessageInput"
          v-model="fields.message"
          placeholder="What are you working on?"
          rows="3"
          max-rows="6"
        ></b-form-textarea>
      </b-form-group>

      <!-- Honeypot -->
      <input
        v-model="honeypot"
        type="text"
        name="subject"
        class="sr-only"
        tabindex="-1"
        autocomplete="off"
      />

      <div class="text-right">
        <b-button type="submit" variant="primary">Submit</b-button>
      </div>
    </b-form>
  </b-modal>
</template>

<script>
import * as ajax from "@/utilities/ajax";
import config from "@/config";

export default {
  name: "ContactForm",
  data() {
    return {
      fields: {
        email: "",
        message: "",
        name: ""
      },
      hideDelay: 5000,
      honeypot: "", // Used as a honeypot
      message: {
        failure: "There was an issue, please try again.",
        success: "Your message was received successfully."
      },
      show: {
        failure: false,
        form: true,
        loading: false,
        success: false
      }
    };
  },
  computed: {
    formData() {
      return Object.assign(
        {
          _email: {
            from: this.fields.name,
            replyto: this.fields.email,
            subject: `Contact: ${this.fields.name}`
          },
          _honeypot: this.subject
        },
        this.fields
      );
    }
  },
  methods: {
    closeModal(message) {
      this.show.loading = false;
      this.show[message] = true;

      setTimeout(() => {
        this.$root.$emit("bv::hide::modal", "contact");
        this.resetForm();
      }, this.hideDelay);
    },
    onSubmit() {
      // Set to Loading
      this.show.form = false;
      this.show.loading = true;

      // Ajax Form Submission
      return ajax
        .post(config.forms.contact, this.formData)
        .then(() => this.closeModal("success"))
        .catch(() => this.closeModal("failure"));
    },
    resetForm() {
      this.fields = {
        email: "",
        message: "",
        name: ""
      };
      this.show = {
        failure: false,
        form: true,
        loading: false,
        success: false
      };
    }
  }
};
</script>
