<template>
  <div class="row justify-content-center no-gutters">
    <div class="col-12">
      <h2>{{ title }}</h2>
    </div>

    <template v-if="playlist === undefined">
      <b-spinner :label="`Loading playlist titled ${title}`"></b-spinner>
    </template>

    <template v-else>
      <div class="col-12 col-sm-6">
        <ul
          v-for="(track, trackIndex) in playlist.tracks"
          v-bind:key="track.id"
          class="playlist list--reset"
        >
          <li
            class="playlist__item"
            :class="{
              'playlist__item--current': songIndex === trackIndex
            }"
          >
            <button
              class="btn--reset d-block w-100 text-left"
              @click="playTrack(url, trackIndex)"
            >
              {{ track.title }}
            </button>
          </li>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import scPlayer from "@/utilities/soundcloud";

export default {
  name: "Playlist",
  props: {
    title: {
      type: String,
      required: true
    },
    url: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapState(["playlists", "playlist_current"]),
    playlist() {
      return this.playlists[this.playlistUri];
    },
    playlistUri() {
      return this.url.split("/").pop();
    },
    songIndex() {
      if (this.playlist_current.uri !== this.playlistUri) return -1;
      return this.playlist_current.index;
    }
  },
  mounted() {
    scPlayer.resolve(this.url, playlist => {
      this.setPlaylist({ playlist, uri: this.playlistUri });
    });
  },
  methods: {
    ...mapMutations(["setCurrentSong", "setPlaylist", "setPlayerVisible"]),

    playTrack(url, trackIndex) {
      scPlayer.resolve(url, () => {
        this.setCurrentSong({ index: trackIndex, uri: this.playlistUri });
        this.setPlayerVisible(true);
        scPlayer.play({ playlistIndex: trackIndex });
      });
    }
  }
};
</script>
